function numberWithCommas(x) {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export default function App() {
  const [order, setOrder] = useState([]);

  var order_number = useRef(0);

  function addToOrder(item) {
    var order_item = JSON.parse(JSON.stringify(item));

    order_number.current = order_number.current + 1;

    console.log(order_number.current);
    order_item.order_number = order_number.current;
    order_item.count = 1;
    //Count logic

    const productIndex = order.findIndex(function (c) {
      return c.name === item.name;
    });

    if (productIndex !== -1) {
      console.log(order[productIndex]);

      const newState = order.map((obj) => {
        //  if id equals 2, update country property
        if (obj.name === item.name) {
          console.log("Adding to the count");
          return { ...obj, count: obj.count + 1 };
        }

        //  otherwise return the object as is
        return obj;
      });

      setOrder(newState);
    } else {
      setOrder((prevArray) => [...prevArray, order_item]);
    }

    // console.log(productIndex);

    // if(productIndex){

    // }
  }

  function removeFromOrder(item) {
    var remove = false;

    const newState = order.map((obj) => {
      //  if id equals 2, update country property
      if (obj.name === item.name) {
        if (obj.count > 1) {
          console.log("Adding to the count");
          return { ...obj, count: obj.count - 1 };
        } else {
          remove = true;
        }
      }

      //  otherwise return the object as is
      return obj;
    });

    if (remove) {
      setOrder((current) =>
        current.filter((order) => order.order_number !== item.order_number)
      );
    } else {
      setOrder(newState);
    }
  }

  var total = 0;

  for (var key in order) {
    var obj = order[key];
    total = total + obj.count * obj.price;
  }

  return (
    <View
      style={{
        flex: 1,
        display: "flex",
        flexDirection: "column",
        font:
          '14px -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif'
      }}
    >
      <View style={styles.container}>
        <View style={styles.leftContainer}>
          <View style={styles.leftTopNavContainer}>
            <Text style={styles.leftContainerTitle}>Items</Text>
          </View>
          <View style={styles.productsContainer}>
            {menu_items.map((item) => (
              <TouchableOpacity
                onPress={() => addToOrder(item)}
                style={styles.productContainer}
              >
                <View style={styles.product}>
                  <Text style={styles.productTitle}>{item.name}</Text>
                  <View style={styles.productImageContainer}>
                    <Image
                      style={styles.productImage}
                      source={{ uri: item.image }}
                    />
                  </View>
                  <View style={styles.productPriceContainer}>
                    <Text style={styles.productPrice}>${item.price}</Text>
                  </View>
                </View>
              </TouchableOpacity>
            ))}
          </View>
        </View>
        <View style={styles.rightContainer}>
          <View style={styles.rightTopNavContainer}>
            <View style={styles.rightTitleContainer}>
              <Text style={styles.rightContainerTitle}>Basket</Text>
            </View>
            <View style={styles.clearBasket}>
              <Text style={styles.clearBasketText}>EMPTY BASKET</Text>
            </View>
          </View>
          <View style={styles.basketProductRowContainer}>
            <View style={styles.basketProductList}>
              {order.map((item) => (
                <TouchableOpacity
                  onPress={() => {
                    removeFromOrder(item);
                  }}
                >
                  <View style={styles.basketProductRow}>
                    <View style={styles.basketProductDescContainer}>
                      <Image
                        style={styles.basketProductImage}
                        source={{ uri: item.image }}
                      />
                      <Text style={styles.basketItemTitle}>{item.name}</Text>
                    </View>
                    <View style={styles.basketProductCountContainer}>
                      <Text style={styles.basketProductCount}>
                        {item.count}x
                      </Text>
                    </View>
                  </View>
                </TouchableOpacity>
              ))}
            </View>
          </View>
        </View>
      </View>
      <View style={styles.bottomContainer}>
        <Text style={styles.totalText}>TOTAL: {numberWithCommas(total)}</Text>
      </View>
    </View>
  );
}

